<template>
  <div class="tab-intro">
    <div class="in-detail-tab-head">
      <span class="icon"></span>
      {{title}}
    </div>
    <slot></slot>
    <div class="box">{{desc}}</div>
  </div>
</template>
<script>
export default {
  props: {
    title: {
        type: String,
        default: "课程介绍"
    },
    desc:{
        type: String,
        default: ""
    }
  },
  data() {
    return {
    };
  }
};
</script>
<style lang="less" scoped>
.tab-intro {
    width: 100%;
    min-height: 310px;
    padding: 0 15px;
    .in-detail-tab-head {
      width: 100%;
      height: 30px;
      line-height: 32px;
      text-align: left;
      color: #63a9ff;
      font-size: 1.1rem;
      .icon {
        width: 3px;
        height: 20px;
        background: #63a9ff;
        display: inline-block;
        vertical-align: middle;
      }
    }
    .box {
      margin-top: 25px;
      text-align: left;
      font-size: 0.9rem;
      color: #888888;
    }
  }
</style>



