<template>
  <div class="side-department border">
    <div class="head">开课机构</div>
    <div class="box">
      <div class="dept">{{deptName}}</div>
      <div class="platform">{{platform}}</div>
      <div class="numbers">
        <span>
          <div class="num">{{lessonNum}}</div>
          <div class="text">课程数</div>
        </span>
        <div class="line"></div>
        <span>
          <div class="num">{{teacherNum}}</div>
          <div class="text">教师</div>
        </span>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      deptName: "睿和科技",
      platform: "资源学习平台",
      lessonNum: 35,
      teacherNum: 108
    };
  }
};
</script>
<style lang="less" scoped>
.side-department {
  width: 280px;
  background: #fff;
  .head {
    width: 100%;
    height: 40px;
    line-height: 40px;
    background: #63a9ff;
    color: #fff;
    padding-left: 20px;
    text-align: left;
  }
  .box {
    width: 100%;
    padding: 15px 40px;
    .dept {
      margin-top: 8px;
      font-size: 1.52rem;
      color: #4a83e2;
      font-weight: 600;
      letter-spacing: 2px;
    }
    .platform {
      font-size: 1.25rem;
      color: #63a9ff;
      letter-spacing: 5px;
    }
    .numbers {
      width: 100%;
      margin-top: 8px;
      display: flex;
      justify-content: space-between;
      span {
        width: 48%;
        display: inline-block;
        .num {
          font-size: 1.1rem;
        }
        .text {
          font-size: 0.8rem;
        }
      }
      .line {
        width: 1px;
        height: 30px;
        background: #a8a8a8;
        align-self: center;
      }
    }
  }
}
</style>



